.container {
  min-width: 450px;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  padding: 10px;
}

.container:hover {
  outline: 1px solid #d0d0d0;
}

.container:active {
  background-color: #e0e0e0;
}

.thumbnail {
  background-color: black;
  border-radius: 20px;
  height: 250px;
  width: 100%;
  object-fit: cover;
}

.titlecontainer {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  padding: 10px;
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
}

.title {
  font-size: 20px;
  padding: 0;
  margin: 0;
  font-weight: 400;
  white-space: normal;
  overflow: hidden;
  text-align: left;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

.description {
  font-size: 16px;
  padding: 10px 0;
  text-align: left;
  display: -webkit-box;
  white-space: pre-wrap;
  -webkit-line-clamp: 2; /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

/* Media Query for Mobile Devices */
@media (max-width: 480px) {
  .container {
    min-width: 300px;
    padding: 5px;
  }

  .thumbnail {
    height: 200px;
    width: 100%;
  }
}

/* Media Query for low resolution  Tablets, Ipads */
@media (min-width: 481px) and (max-width: 767px) {
  .container {
    min-width: 300px;
    padding: 5px;
  }

  .thumbnail {
    height: 200px;
    width: 100%;
  }
}
